<template>
  <div class="card min-vh-50">
    <div class="card-header pb-0">
      <div class="d-flex justify-content-between">
        <div class="d-flex gap-4 align-items-center">
          <h4>Event Collections</h4>
          <div v-if="tableProgress.loading" class="spinner-border spinner-border" role="status"></div>
        </div>
        <button-component
          color="success"
          size="sm"
          variant="outline"
          :isLoading="tableProgress.loading"
          :isDisabled="tableProgress.loading"
          @click="toggleAddModal()"
        >
          Add Collection
        </button-component>
        <AddCollection v-if="modals.addCollection" />
      </div>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table v-if="!tableProgress.loading" class="table align-items-center mb-0">
          <thead>
            <tr>
              <th class="text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">Name</th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">
                Creation Date
              </th>
              <th class="text-center text-uppercase text-secondary text-xxs font-weight-bolder opacity-7">
                Update Date
              </th>
              <th class="text-secondary opacity-7"></th>
              <th class="text-secondary opacity-7"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="collection in collectionList" :key="collection.id">
              <td>
                <div class="d-flex px-2 py-1">
                  <div class="d-flex flex-column justify-content-center">
                    <h6 class="mb-0 text-sm">{{ collection.name }}</h6>
                  </div>
                </div>
              </td>
              <td>
                <p class="text-center text-xs font-weight-bold mb-0">{{ getFormattedDate(collection.createAt) }}</p>
              </td>
              <td>
                <p class="text-center text-xs font-weight-bold mb-0">{{ getFormattedDate(collection.updateAt) }}</p>
              </td>
              <td class="align-middle text-center">
                <a
                  class="btn btn-link text-dark"
                  @click="toggleEditModal({ name: collection.name, id: collection.id })"
                >
                  <i class="fas fa-pencil-alt text-dark me-2" aria-hidden="true"></i>Edit
                </a>
              </td>
              <td class="align-middle">
                <a class="btn btn-link text-danger text-gradient" @click="toggleDeleteModal(collection.id)">
                  <i class="far fa-trash-alt me-2" aria-hidden="true"></i>Delete
                </a>
              </td>
            </tr>
          </tbody>
        </table>
        <EditCollection v-if="modals.editCollection" />
        <deleteCollection v-if="modals.deleteCollection" />
      </div>
    </div>
  </div>
</template>

<script>
  import ButtonComponent from '@/components/Button.vue';
  import moment from 'moment';
  import { defineAsyncComponent } from 'vue';

  export default {
    name: 'collection-table',

    components: {
      ButtonComponent,
      AddCollection: defineAsyncComponent(() => import('./collection-actions/add-collection')),
      EditCollection: defineAsyncComponent(() => import('./collection-actions/edit-collection')),
      deleteCollection: defineAsyncComponent(() => import('./collection-actions/delete-collection')),
    },

    computed: {
      tableProgress() {
        return this.$store.state.CollectionStore.progress.collectionList;
      },
      modals() {
        return this.$store.state.CollectionStore.modals;
      },
      collectionList() {
        return this.$store.state.CollectionStore.collectionList.collections;
      },
    },

    methods: {
      getFormattedDate: (date) => moment(date).format('YYYY-MM-DD HH:mm'),

      toggleAddModal() {
        this.$store.commit('CollectionStore/TOGGLE_ADD_COLLECTION_MODAL');
      },

      toggleDeleteModal(collectionId) {
        this.$store.commit('CollectionStore/SET_COLLECTION_ID', collectionId);
        this.$store.commit('CollectionStore/TOGGLE_DELETE_COLLECTION_MODAL');
      },
      toggleEditModal(collectionDetails) {
        this.$store.commit('CollectionStore/SET_EDIT_COLLECTION_DETAILS', collectionDetails);
        this.$store.commit('CollectionStore/TOGGLE_EDIT_COLLECTION_MODAL');
      },
    },
  };
</script>
